body {
  scroll-behavior: smooth;
  font-family: 'Product Sans', Helvetica, Arial, sans-serif;
}
.outerdiv {
  background-color: #2dd465;
  height: 100dvh;
  width: 100%;
}
.extra {
  width: 100%;
  height: 12vh;
}
.nav1 {
  margin-left: 18px;
  margin-top: 10px;
  background: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.25);
  width: 98%;
  height: 80px;
  border-radius: 13px;
  top: 10px;
  position: sticky;
  z-index: 20;
}
.nav{
  width: 100%;
  background: #fff;
  padding: 20px;
  display: flex;
  justify-content: space-between;

}
.navspace {
  height: 15vh;
  width: 100%;
  display: flex;
  justify-content: center;
  padding-top: 15px;
}

.nav > p {
  font-size: 24px;
  font-weight: bold;
}
.Logo {
  height: 10dvh;
}
.menubar {
  height: 24px;
  /* align-self: center; */
}
.navBar {
  width: 80%;
  max-width: 600px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.iamOwner {
  background: #fff;
  font-size: 16px;
  padding: 7px;
  border-radius: 6px;
  border: 2px solid #2dd465;
  color: #2dd465;
  cursor: pointer;
}
.iamOwner:hover {
  border: 2px solid #13544f;
  background: #13544f;
  color: #fff;
  transition: 0.3s;
}
.navBar a {
  display: inline-block;
  font-size: 1.2rem;
  font-weight: 400;
  text-decoration: none;
  color: #2dd465;
}
.navBar a::after {
  content: '';
  display: block;
  height: 3px;
  width: 0;
  background-color: #13544f;
  transition: width 0.3s;
}
.navBar > a:hover::after {
  width: 100%;
}
.navBar a:hover {
  color: #13544f;
}
.mainbody {
  height: 54dvh;
  background-color: #fff;
  text-align: center;
}
.big {
  font-size: 3.5rem;
  font-weight: bold;
  text-transform: uppercase;
}
.small {
  font-weight: bold;
  font-size: 1.3rem;
  margin: 16px 0 16px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(7, 49, 38, 0.75);
}
.small1 {
  font-size: 0.9rem;

  color: rgba(7, 49, 38, 0.75);
}
.small img {
  margin-left: 10px;
}
.searchBox {
  width: 90%;
  background-color: rgb(255, 255, 255);
  max-width: 1000px;
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 58.5dvh;
  left: 50%;
  transform: translate(-50%, 0);
  box-shadow: 0 -2px 20px -6px #707070;
  z-index: 15;
}
.locationInput {
  text-align: start;
  height: 6rem;
  width: 40%;
  padding: 10px;
}
.locationInput > input {
  height: 2rem;
  font-size: 1.2rem;
  padding: 2px;
  border: 0;
  outline: none;
  margin-top: 10px;
  width: 90%;
}
.outerinput {
  width: 80%;
  display: flex;
  justify-content: space-around;
}
.buttonsearch {
  font-size: 1.4rem;
  width: 20%;
  height: 6rem;
  background-color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  cursor: pointer;
}
.buttonsearch:hover {
  background-color: #545454;
}

.searchfirstcityresult {
  cursor: pointer;
  width: 100%;
  color: rgba(7, 49, 38, 0.75);
  background-color: #fff;
  margin-top: 20px;
  border-radius: 8px;
}
.searchresult {
  border-bottom: 2px solid #c7c6c6;
  border: 3px solid #c6dacd;
  border-radius: 9px;
}

.searchresult h1 {
  font-weight: 300;
  font-size: 1.5rem;
}

.MainMobilenavParent {
  width: 90%;
  height: 30dvh;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: absolute;
  left: 5%;
  padding: 10px;
  box-shadow: 0px 5px 15px -8px #707070;
}
.mobilenavchild {
  display: flex;
  justify-content: center;
  align-items: center;
}
.mobilenavchild > a {
  text-decoration: none;
  color: #333232;
}
.mobilenavchild > a::after {
  content: '';
  display: block;
  height: 3px;
  width: 0;
  background-color: #13544f;
  transition: width 0.3s;
}
.mobilenavchild > a:hover::after {
  width: 100%;
}
.mobilenavchild > a:hover {
  color: #13544f;
}
.section {
  height: 100dvh;
}
.image {
  height: 100dvh;
}
.space {
  height: 3vw;
}

.lowertextinmainbody {
  font-size: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-weight: bold;
  position: relative;
  top: 20svh;
}
.lowertextinmainbody p {
  font-size: 1rem;
  position: relative;
  top: -5svh;
}
.slidercontainer {
  background-color: #fff;
  text-align: center;
  height: 160px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.heading p {
  font-size: 37px;
  font-weight: bold;
  color: #2dd465;
  margin: 25px 0;
}
.slider {
  height: 100px;
  width: 100%;
  background: #ffffff;
}
.outerSlider {
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.25);
}
.sliderimage {
  height: 100px;
  width: 95%;
}
.clintspace {
  width: 100%;
  height: 2vh;
}
.heading p::after {
  content: '';
  height: 6px;
  width: 145px;
  background-color: #2dd465;
  border-radius: 25px;
  display: block;
  margin: auto;
}

.features {
  width: 100%;
  height: 43svh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}
.features img {
  height: 30svh;
  width: 30svh;
  border-radius: 50%;
}
.featurecontainer {
  width: 30svw;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 40svh;
}
.features p {
  font-size: 1rem;
  font-weight: bolder;
  color: rgba(7, 49, 38, 0.75);
}
.Insourancefeatures {
  width: 100%;
  height: 80svh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 60px;
}
.InsourancefeaturesContainer {
  width: 80%;
  height: 80%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.25);
}
.InsourancefeaturesContainer img {
  height: 80%;
}
.insouranceconatiner {
  height: 80%;
  width: 40%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.insouranceconatiner h1 {
  font-size: 2.2rem;
  color: #2dd465;
}
.insouranceconatiner p {
  width: 80%;
  font-size: 1.1rem;
  color: rgba(7, 49, 38, 0.75);
}
.staticcomponent {
  height: 15svh;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.staticcomponent h1 {
  font-size: 3rem;
}

.faq {
  width: 100%;
  height: 100dvh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding-top: 120px;
}
.faq h1 {
  font-size: 35px;
  color: #2dd465;
}
.faq h1::after {
  content: '';
  height: 6px;
  width: 81px;
  background-color: #2dd465;
  border-radius: 25px;
  display: block;
  margin: auto;
}
.faqcontainer {
  width: 60%;
}
.question {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.6rem;
  padding: 20px;
  color: rgba(7, 49, 38, 0.75);
}
.outerqna {
  border-bottom: 2px solid rgba(7, 49, 38, 0.75);
}
.questiontext {
  width: 96%;
  color: rgba(7, 49, 38, 0.75);
}
.answer {
  width: 80%;
  margin: auto;
  margin-bottom: 20px;
  font-size: 1.2rem;
  color: rgba(7, 49, 38, 0.75);
}
.orderform{
  position: fixed;
  width: 36%;
  height: auto;
  background-color: #ffffff;
  top: 0;
  left: 50%;
  transform: translate(-50%, 50%);
  z-index: 50;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px;
  border-radius: 10px;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.25);
}
.orderforms{
  position: fixed;
  width: 36%;
  height: auto;
  background-color: #ffffff;
  top: 0;
  left: 50%;
  transform: translate(-50%, 50%);
  z-index: 50;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px;
  border-radius: 10px;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.25);
}
.orderformss{
  position: fixed;
  width: 36%;
  height: auto;
  background-color: #ffffff;
  top: 0;
  left: 50%;
  transform: translate(-50%, 50%);
  z-index: 50;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px;
  border-radius: 10px;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.25);
}
.radios{
  display: inline-flex;
  overflow: hidden;
  border-radius: 15px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
  margin-top: 23px;
}
.radio__input{
  display: none;
}
.radio__label{
  padding: 8px 14px;
  font-size: 14px;
  cursor: pointer;
  background-color: #1ed75bab;
  color: white;
}
.radio__label:not(:last-of-type){
  border-right: 3px solid #38a05b;
}
.radio__input:checked+ .radio__label{
  background-color: #2dd465;
}
form .field{
  height: 28px;
  width: 100%;
  margin-top: 23px;
}
form .field{
  height: 28px;
  width: 100%;
  margin-top: 23px;
}
form .fields{
  width: 55%;
  display: flex;
  justify-content: center;
}
form .field input{
    height: 100%;
    width: 230px;
    outline: none;
    font-size: 18px;
    border-radius: 5px;
    color:#21763dab;
    border: 1px solid #7de39f;
    border-bottom-width: 3px;
    transition: all 0.4s ease;
    background: rgba(0,0,0,0.1);
    box-shadow: 0 0 5px rgba(0,0,0,.3) inset;  
}
.padding{
  padding-left: 9px;
}
.padding::-webkit-input-placeholder{
  color:#21763dab;
}
form .field input:focus{
  border-color: #2dd465;
}
form .field input[type="submit"]{
  width:98px;
  font-size: 17px;
  font-weight: 50;
  cursor: pointer;
  box-shadow: none;
  background: #fff;
  border-radius: 6px;
  border: 2px solid #2dd465;
  color: #2dd465;
  cursor: pointer;
}
form .field input[type="submit"]:hover{
  color: #fff;
  background: #2dd465;
}
.close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  font-size: 25px;
}
.close:hover{
  color:gray;
}
@media screen and (max-width: 720px) {
  .orderforms-activate{
    width: 60%;
  }
  .small1 {
    font-size: 0.7rem;
  }
  .mainbody {
    height: 67dvh;
    background-color: #fff;
    padding: 55px;
    text-align: center;
  }
  .outerdiv {
    height: 110svh;
  }
  .lowertextinmainbody {
    font-size: 2.5rem;
  }
  .lowertextinmainbody p {
    top: -3svh;
    font-size: 1rem;
  }

  .searchBox {
    width: 90%;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 70.5dvh;
    left: 50%;
    transform: translate(-50%, 0);
  }
  .outerinput {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
  }
  .locationInput {
    text-align: start;
    height: 4rem;
    width: 100%;
    padding: 10px;
    border-bottom: 1px solid #333;
  }
  .buttonsearch {
    font-size: 1.4rem;
    width: 100%;
    height: 3rem;
  }
  .locationInput > input {
    height: 1.2rem;
    font-size: 0.8rem;
    padding: 2px;
    border: 0;
    outline: none;
    margin-top: 10px;
  }
  .big {
    font-size: 2.5rem;
    font-weight: bold;
  }
  .small {
    font-weight: bold;
    font-size: 0.8rem;
  }
  .outerSlider {
    width: 100%;
  }
  .features {
    width: 100%;
    height: auto;
    flex-direction: column;
  }

  .featurecontainer {
    width: 100%;
    height: 40svh;
  }
  .featurecontainer p {
    width: 80%;
  }
  .InsourancefeaturesContainer {
    flex-direction: column;
    width: 90%;
  }
  .Insourancefeatures {
    height: 120svh;
  }
  .insouranceconatiner {
    width: 80%;
    justify-content: center;
  }
  .insouranceconatiner h1 {
    font-size: 1.9rem;
  }
  .InsourancefeaturesContainer img {
    width: 90%;
    height: auto;
  }
  .InsourancefeaturesContainer p {
    width: 100%;
  }

  .staticcomponent h1 {
    font-size: 2rem;
  }
  .questiontext {
    font-size: 1.2rem;
  }
  .answer {
    font-size: 1rem;
  }
  .searchfirstcityresult {
    position: absolute;
    width: 80%;
    background-color: #fff;
    margin-top: 5px;
  }
  .searchresult {
    border-bottom: 2px solid #c7c6c6;
  }

  .searchresult h1 {
    font-weight: 300;
    font-size: 1.2rem;
  }
  .orderform {
    width: 90%;
    padding: 2px;
  }
  .orderformss {
    width: 90%;
    padding: 2px;
  }
  form .field input[type="submit"]{
    width: 87px;    
  }
  .eachformcomponent {
    width: 90%;
  }
  .close {
    position: absolute;
    top: 5px;
    right: 5px;
  }
}
.servicecontainer {
  width: 100%;
  height: 100dvh;
}

@media screen and (max-width:1045px) {
  .container-m{
    width: 100%;
    height: 75dvh;
  }
}
@media screen and (max-width:427px) {
  .container-m{
    width: 100%;
    height: 86dvh;
  }
}
footer {
  width: 100%;
  position: sticky;
  top: 100%;
  text-align: center;
  color: aliceblue;
  background-color: #333232;
}
.spaceSM {
  width: 100%;
  height: 4vh;
}
@media screen and (max-width: 1391px) {
  .spaceSC {
    width: 100%;
    height: 11vh;
  }
}
@media screen and (max-width: 1198px) {
  .spaceSC {
    width: 100%;
    height: 20vh;
  }
}
@media screen and (max-width: 1046px) {
  .servicecontainer{
    width: 100%;
    height: 190dvh;
  }
}
@media screen and (max-width: 1045px) {
  .faqcontainer {
    width: 90%;
  }
}
@media screen and (max-width:900px) {
  .section{
    height:122dvh;
  }
}
@media only screen and (max-width: 938px) and (min-width: 785px){
  .space{
    height: 90px;
  }
}
@media only screen and (max-width: 1600px) and (min-width: 900px) {
  ::-webkit-scrollbar {
    width: 10px;
  }
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  ::-webkit-scrollbar-thumb {
    background: #888;
  }
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
}
